/* instructions */

.instructions {
    background: #393F7D;
    width: 780px;
    height: 50px;
    padding: 5px 10px 20px 10px;
    position: relative;
    top: 0px;
    z-index: 50;
}


/* header bar */

.header {
    background: #1B1F4E;
    width: 800px;
    height: 31px;
    font-family: sans-serif;
    color: #f1f1f1;
    z-index: 60;
    position: relative;
    top: 0px;
}

.titles {
    float: left;
    margin-top: -15px;
}

.menu {
    float: right;
}

.sele{
  float: left;
  width: 188px;
  height: 25px;
  padding: 5px 5px 11px 5px;
}

span{
	font-size: 0.75em;
}

.traitsButton:focus {
    border: 2px solid #EF9719;
    outline: none;
}

/* non-selected class */
.red,
.traitsButton {
	background: #5B5B5B;
	color: #ffffff;
	border: none;
	cursor: pointer;
	float: left;
	margin-right: 5px;
	padding: 7px;
	transition: background linear .2s;
	outline: none;
}

/* selected class */

.green {
    background: green;
    outline: none;
}

/* checkboxs main div */
.traits {
    width: 800px;
    height: 600px;
    background: #ffffff;
    position: relative;
    top: 0px;
    left: 0px;
    padding: 10px;
}

.traits h3{
    color: #1B1F4E;
}

.question{
    width: 80%;
    margin: 0 auto;
    padding: 10px;
    height: 90px;
}

.navbuttons{
    width: 80%;
    margin: 0 auto;
    padding: 10px;
}

.Prev{
    float: left;
}

.Next, .Fin{
    float: right;
}
.question h2{
    color: #ED5901;
    font-size: 150%;
}

.scaleArea{
    width: 80%;
    margin: 40 auto;
    padding: 20px;
}

.titless{
    width: 80%;
    margin: 0 auto;
    padding: 20px;
    margin-bottom: 10px;
}

.cs{
    float: left;
    font-size: 150%;
    color: #393F7D;
}

.sc{
    float: right;
    font-size: 150%;
    color: #393F7D;
}


/* buttons */

/*ui handle */
.ui-slider-handle{
    width: 40px !important;
    height: 40px !important;
    font-size: 200% !important;
    text-align: center;
    padding: 10px;
    top: -20px !important;
}

.scaleButtons{
    border: none;
    width: 12.71%;
    height: 14%;
    background: #EF9719;
    color: #ffffff;
    font-size: 200%;
    margin-right: 10px;
    cursor: pointer;
}

.scaleButtons:hover{
    background: #ED5901;
}
.btn {
    /* background: #D4001D; */
    border: none;
    margin-right: 1px;
    color: #ffffff;
    display: inline;
    padding: 5px 5px 7px 5px;
    cursor: pointer;
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ea001f+0,bf0019+100 */
    background: rgb(234, 0, 31);
    /* Old browsers */
    background: -moz-linear-gradient(top, rgba(234, 0, 31, 1) 0%, rgba(191, 0, 25, 1) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(234, 0, 31, 1) 0%, rgba(191, 0, 25, 1) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(234, 0, 31, 1) 0%, rgba(191, 0, 25, 1) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='#ea001f', endColorstr='#bf0019', GradientType=0);
    /* IE6-9 */
}

.btn:hover {
    background: #ED5901;
}

